const { css } = require('styled-components')

// screen sizes
const sizes = {
  desktopLarge: 1200,
  desktopMedium: 992,
  desktopSmall: 768,
  mobileLarge: 425,
  mobileMedium: 375,
  mobileSmall: 329,
}

// responsive media queries
export default {
  /**
   * 尺寸大於 1200
   */
  large: (...args: any) => css`
    @media all and (min-width: ${sizes.desktopLarge}px) {
      ${css(...args)};
    }
  `,

  /**
   * 尺寸介於 992 和 1200 之間
   */
  medium: (...args: any) => css`
    @media all and (min-width: ${sizes.desktopMedium}px) and (max-width: ${sizes.desktopLarge -
      1}px) {
      ${css(...args)};
    }
  `,

  /**
   * 768 <= and <= 992
   */
  small: (...args: any) => css`
    @media all and (min-width: ${sizes.desktopSmall}px) and (max-width: ${sizes.desktopMedium -
      1}px) {
      ${css(...args)};
    }
  `,

  /**
   * 尺寸小於 767
   */
  extraSmall: (...args: any) => css`
    @media all and (max-width: ${sizes.desktopSmall - 1}px) {
      ${css(...args)};
    }
  `,

  /**
   * 尺寸小於 425
   */
  mobileLarge: (...args: any) => css`
    @media all and (max-width: ${sizes.mobileLarge}px) {
      ${css(...args)};
    }
  `,

  /**
   * 尺寸小於 375
   */
  mobileMedium: (...args: any) => css`
    @media all and (max-width: ${sizes.mobileMedium}px) {
      ${css(...args)};
    }
  `,

  /**
   * 尺寸小於 329
   */
  mobileSmall: (...args: any) => css`
    @media all and (max-width: ${sizes.mobileSmall}px) {
      ${css(...args)};
    }
  `,

  /**
   * 直立
   */
  orientation: (...args: any) => css`
    @media all and (orientation: portrait) {
      ${css(...args)};
    }
  `,

  /**
   * 水平
   */
  orientationLandscape: (...args: any) => css`
    @media all and (orientation: landscape) {
      ${css(...args)};
    }
  `,
}
